import { reactive } from "vue"
import axios from "./axios"

const auth = reactive({
  isLoggedIn: false,
  isStaff: false,
  isInitialized: false, // Add initialization flag

  init() {
    this.isLoggedIn = !!localStorage.getItem("access")
    this.isStaff = localStorage.getItem("is_staff") === "true"
    this.isInitialized = true // Mark as initialized
  },

  async checkTokenValidity() {
    // Skip check in demo mode
    if (process.env.VUE_APP_DEMO_MODE === "true") {
      this.isInitialized = true
      return true
    }

    const token = localStorage.getItem("access")
    if (!token) {
      this.isInitialized = true
      return false
    }

    try {
      await axios.get("user/")
      this.isInitialized = true
      return true
    } catch (error) {
      console.log("Token invalid or expired, logging out")
      this.logout()
      return false
    }
  },

  logout(redirectToGithubPages = false) {
    const cart = localStorage.getItem("cart")
    localStorage.clear()
    if (cart) localStorage.setItem("cart", cart)

    this.isLoggedIn = false
    this.isStaff = false
    this.isInitialized = true

    if (redirectToGithubPages) {
      // This will be handled by the component using router
    }
  },

  // Add method to wait for initialization
  async waitForInitialization() {
    return new Promise((resolve) => {
      if (this.isInitialized) {
        resolve()
        return
      }

      const checkInterval = setInterval(() => {
        if (this.isInitialized) {
          clearInterval(checkInterval)
          resolve()
        }
      }, 50)
    })
  },
})

// Initialize immediately
auth.init()

export { auth }
